import React from 'react'
import profile from '../images/profile.jpg'
import '../css/nav.css'
import { Link } from 'react-router-dom'

export default function Nav({ lang, dark }) {
    const text = { en: [ 'About', 'Work', 'Hire Me!' ], es: [ 'Sobre mí', 'Experiencia', '¡Contrátame!' ] }

    function changeLang() {
        if (lang.current === 'es') {
            lang.current = 'en'
        }
        else {
            lang.current = 'es'
        }
    }

    return (
        <nav className={ (dark ? "darkmode" : null) }>
            <Link className="logocont" to="/" >
                <img src={ profile } alt="Profile" className="logo" />
                <h1>frank</h1>
            </Link>
            <div className="buttons">
                <Link to="/about" className='button'>
                    { text[ lang.current ][ 0 ] }
                </Link>
                <Link to="/work/" className='button'>
                    { text[ lang.current ][ 1 ] }
                </Link>
                <Link to='/hire/' className='button hirebtn'>
                    { text[ lang.current ][ 2 ] }
                </Link>
                <Link className="lang" onClick={ changeLang }>{ lang.current.toUpperCase() }</Link>
            </div>
        </nav>
    )
}
